	module.exports = {"data":{"markdownRemark":{"id":"/Users/cgreen/Documents/Projects ðŸ’© ðŸ‘» /codeSquawk/content/posts/2018-01-29--first-one-down/index.md absPath of file >>> MarkdownRemark","html":"<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/levi-saunders-327323-2525dd0446f7e34cbf714def23aba9d2-00762.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 66.66%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Photo by Levi Saunders on Unsplash\"\n        title=\"\"\n        src=\"/static/levi-saunders-327323-2525dd0446f7e34cbf714def23aba9d2-893a4.jpg\"\n        srcset=\"/static/levi-saunders-327323-2525dd0446f7e34cbf714def23aba9d2-bbf57.jpg 200w,\n/static/levi-saunders-327323-2525dd0446f7e34cbf714def23aba9d2-260b8.jpg 400w,\n/static/levi-saunders-327323-2525dd0446f7e34cbf714def23aba9d2-893a4.jpg 800w,\n/static/levi-saunders-327323-2525dd0446f7e34cbf714def23aba9d2-25965.jpg 1200w,\n/static/levi-saunders-327323-2525dd0446f7e34cbf714def23aba9d2-e54c8.jpg 1600w,\n/static/levi-saunders-327323-2525dd0446f7e34cbf714def23aba9d2-7c0ed.jpg 2400w,\n/static/levi-saunders-327323-2525dd0446f7e34cbf714def23aba9d2-00762.jpg 5000w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Well, we finished our first stream.</p>\n<p><a href=\"https://youtu.be/WeBUdmNEdRI\"><em>Hereâ€™s a link of the video if you want to see.</em></a></p>\n<p>It did not go as well as had hoped.<br>\nI stuttered, only recorded in 480p, was quiet for too long, forgot much of what I wanted to do and say, and blanked on basic HTML programming that I never should have forgotten in the first place. <br>\nHowever, we got through it! And thatâ€™s what matters!</p>\n<p>Just as I said in a <a href=\"https://codesquawk.me/hello-world/\">previous post</a></p>\n<blockquote>\n<p>Doing something is the first step to learning it.</p>\n</blockquote>\n<p>So, some things I learned today</p>\n<ul>\n<li>Input will always be a string</li>\n<li>\n<p>In Vanilla JS at least, there is no decent way to manipulate an interval to start or stop it.</p>\n<ul>\n<li>The solution we came up with was to declare the variable outside the function and call it that way.</li>\n</ul>\n</li>\n<li>I forgot how to create a basic form so we first used <code>document.querySelector</code> on the form itself and caught the submit action, then used <code>document.querySelector</code> on the input and received the input that way. I feel like there should be a better way of doing it.</li>\n</ul>\n<p>I uploaded everything we did on github <a href=\"https://github.com/Ecleptic/Interval-Timer/tree/093586ec2787a837d5dbd35b45d5e6fa6afc7133\"><strong>HERE</strong></a> if you would like to take a look.</p>\n<p>As always, Iâ€™d love to hear what you guys think so far! Go ahead and use the contact form at the bottom ðŸ‘‡ and let me know what you think!</p>\n<p>Thanks Everyone!</p>\n<p>Cameron</p>","frontmatter":{"title":"First One Down","subTitle":"We did it!","date":"January 29, 2018","cover":{"childImageSharp":{"resize":{"src":"/static/levi-saunders-327323-2525dd0446f7e34cbf714def23aba9d2-ada8c.jpg"}}}}}},"pathContext":{"path":"/first-one-down/"}}

